import React from "react";
import styled from "styled-components";
import { graphql } from "gatsby";
import Project from "@components/page/Project";
import { SeoContainer } from "@components/Container/seoContainer";
import { graphqlAllPostType } from "@tool/types";

type ProjectListPagePropTypes = {
  data: graphqlAllPostType;
};

const Layout = styled.main`
  padding: 32px;
`;

export default function ProjectsListPage({
  data, // this prop will be injected by the GraphQL query below.
}: ProjectListPagePropTypes) {
  const articles = data.articles.edges.map((v: any) => v.node);

  console.log({ articles });
  return (
    <Layout>
      <Project list={articles} />
    </Layout>
  );
}
export const pageQuery = graphql`
  query ($language: String!) {
    locales: allLocale(filter: { language: { eq: $language } }) {
      edges {
        node {
          ns
          data
          language
        }
      }
    }
    articles: allMarkdownRemark(
      filter: {
        fields: {
          sourceInstanceName: { eq: "project" }
          locale: { eq: $language }
        }
      }
    ) {
      edges {
        node {
          fields {
            name
          }
          frontmatter {
            title
            date
            image_cover
            type
          }
          html
        }
      }
    }
  }
`;

export const Head = () => (
  <SeoContainer subtitle="project" pathname="/project" />
);
